'use client';
import { ThemeProvider, useTheme } from '../contexts/ThemeContext';
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Cookies from 'js-cookie';




export default function Login() {
  const router = useRouter();
  const token = "{'login':'marcio.cezar'}";
  const [error, setError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const username = formData.get('loginuser');
    const password = formData.get('password');

    // Validação básica (substituir por API real)
    if (username === 'admin' && password === 'admin123') {
      setError('');
      
      Cookies.set('authToken', token);
      router.push('/default');
    } else {
      setError('Senha ou Email invalidos. Tente novamente.');
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <div className="max-w-md w-full space-y-8 p-8 bg-white rounded-lg shadow">
        <div>
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Login
          </h2>
        </div>
        {/* ERRO 1 CORRIGIDO: O nome da função estava errado ("hadleSubmit" em vez de "handleSubmit") */}
        <form className="mt-8 space-y-6" onSubmit={handleSubmit}>
          <div className="rounded-md shadow-sm space-y-4">
            <div>
              <label htmlFor="loginuser" className="sr-only">Usuário</label>
              <input
                id="loginuser"
                name="loginuser"
                type="text"
                required
                className="appearance-none rounded relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                placeholder="Usuário"
              />
            </div>
            <div>
              <label htmlFor="password" className="sr-only">Senha</label>
              <input
                id="password"
                name="password"
                type="password"
                required
                className="appearance-none rounded relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                placeholder="Senha"
              />
            </div>
          </div>

          {/* ERRO 3 CORRIGIDO: Adicionado um local para exibir a mensagem de erro */}
          {error && (
            <p className="text-center text-sm text-red-600">
              {error}
            </p>
          )}

          <div>
            <button
              type="submit"
              className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              Entrar
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}